import React, { useState , useContext, useEffect} from 'react'
import Alert from 'react-bootstrap/Alert';
import { AuthContext } from '../context';
import { useNavigate } from "react-router-dom";



const Login = () => {
  let navigate = useNavigate();
  useEffect(() => {
    if (localStorage.getItem('auth') === '1'){
      navigate('/home')
    }
  })

 
  const trueLogin = 'ybabuevoli'
  const [login, setLogin] = useState('')
  const [alert, setAlert] = useState(false)
  const {isAuth, setIsAuth} = useContext(AuthContext)



  const checkLogin = () => {
    if (login === trueLogin){
      setIsAuth(1)
      localStorage.setItem('auth', '1')
      navigate('/home')

    }
    else{
      setAlert(true)
      setTimeout(() => {
        setAlert(false)
      }, 5000);
    }
  }

  return (
    <div className='full_block login_page'>
      <div className="full_home">
        {alert &&
          <Alert variant={'danger'}>
          Неправильно, попробуй еще раз дорогая!!!
        </Alert>
        }
        <div className="content">Привет! Сегодня твой день рождения и я приготовил для тебя небольшой сюрприз!</div>
        <div className="content">Твое первое задание следуй за радугой чтобы получить пароль!</div>
        <input type="text" onChange={e => setLogin(e.target.value)}/>
        <button onClick={() => checkLogin()}>Подтвердить</button>
      </div>
    </div>
  )
}

export default Login