import React, { useState } from 'react'

const Map = ({progress, setProgress, scores, setScores}) => {

    const [pazle, setPazle] = useState([
        {visible: false, img: ''},
        {visible: false, img: ''},
        {visible: false, img: ''},
        {visible: false, img: ''},
        {visible: false, img: ''},
        {visible: false, img: ''},
        {visible: false, img: ''},
        {visible: false, img: ''},
        {visible: false, img: ''},
        {visible: false, img: ''},
        {visible: false, img: ''},
        {visible: false, img: ''},
        {visible: false, img: ''},
        {visible: false, img: ''},
        {visible: false, img: ''},
        {visible: false, img: ''},
        {visible: false, img: ''},
        {visible: false, img: ''},
    ])

    const check = (target) => {
      const id = target.getAttribute('name')
      if (scores >= 2){
        setScores(scores-2)
        let copy = Object.assign([], pazle);
        copy[parseInt(id)].visible = true
        setPazle(copy)
        setProgress(progress + 1)
      }
    }

    

  return (
    <div className='present_map'>
        {pazle.map( (e, index) =>
            e.visible
            ? <div key={index} className={'pazle pazle' + (index)}></div>
            : <div onClick={(e) => check(e.target) } name={index} key={index} className='pazle pazle_vopros'></div>
    )}
    </div>
  )
}

export default Map