import React, { useContext, useEffect, useState } from 'react'
import Map from '../components/Map'
import Presents from '../components/Presents'
import Quests from '../components/Quests'
import Rules from '../components/Rules'
import { AuthContext } from '../context'

const Home = () => {
  const [scores, setScores] = useState(0)
  const [rules, setRules] = useState(false)
  const [progress, setProgress] = useState(0)
  const [presents, setPresents] = useState(false)

  
  useEffect(() => {
  if(!localStorage.getItem('launch')){
    localStorage.setItem('launch', 'true')
    setRules(true)
  }
}, [])

  useEffect(() => {
    if(progress === 5 || progress === 10 || progress === 18){
      setPresents(true)
    }
  }, [progress])

  

  return (
    <div className='home'>
        <div className="full_home">
          <h1>Игра в попугая</h1>
          <div className="myrow">
              <Map progress={progress} setProgress={setProgress} scores={scores} setScores={setScores}/>
              <Quests scores={scores} setScores={setScores}/>
          </div>
          <div className="total_row">
            <div className="">
              <div className='total'>Всего очков: {scores}</div>
              <div className='total_desc'>P.s. для открытия ячейки нужно 2 очка</div>
            </div>
            <button onClick={() => setRules(true)}>Правила</button>
          </div>
          {rules && <Rules scores={scores} setScores={setScores} setRules={setRules}/>}
          {presents && <Presents progress={progress} setPresents={setPresents} />}
        </div>
    </div>
  )
}

export default Home