import React, { useState } from 'react'

const Rules = ({scores, setScores, setRules}) => {

  const password = 'romeo1011'
  const [input, setInput] = useState(false)
  const [value,setValue] = useState('')

  const risk = () => {
    if(!input){
    setInput(true)}
    else{
      if(value === password){
        setScores(scores+1)
        setInput(false)
        setValue('')
      }
    }
  }

  return (
    <div onClick={() => setRules(false)} className="rules">
        <div className="rules_content" onClick={e => e.stopPropagation()}>
            <h1 style={{marginBottom: '30px'}}>Правила</h1>
            <div className="rules_text">
                <ul>
                    <li>Первое правило бойцовского клуба - никому не говори о бойцовском клубе!</li>
                    <li>За каждый вопрос ты получаешь по 2 балла</li>
                    <li>Для разблокировки одной ячейки требуется также 2 балла</li>
                    <li>Все ответы указывать исключительно на русском языке</li>
                    <li>Также ты можешь испытать удачу и продать душу дьяволу</li>
                    <li>Не обновляй страницу, а то потеряешь весь прогресс</li>
                </ul>
                Больше никаких правил. Желаю успешного прохождения Игрок! 
                <br />Выживет сильнейший!
                <div className="hr_rules"></div>
                <div className="row_rules">
                  {input
                    ?  [ <input value={value} onChange={(e) => setValue(e.target.value)} type='password' placeholder='КЛЮЧ'/>,
                        <button onClick={() => risk()}>Подтвердить</button>]
                    :  [<div className="">Продать душу дьяволу за 1 очко</div>,
                        <button onClick={() => risk()}>Рискнуть</button>]
                  }
                 
                </div>
            </div> 
        </div>
    </div>
  )
}

export default Rules