import React, { useState } from 'react'
import Quest from './Quest'

const Quests = ({setScores,scores}) => {
    const [quests, setQuest] = useState([
        {quest: 'Какой у нас слоган?', answer: 'пальчик верни', status: false},
        {quest: 'Какого числа ты впервые поцеловала своего принца?', answer: '23', status: false},
        {quest: 'Какая любимая змея у твоего парня?', answer: 'питон', status: false},
        {quest: 'В каком городе мы в первый раз снимали отель?', answer: 'москва', status: false},
        {quest: 'Угадай загадку: "Кругом волоса, посредине колбаса"', answer: 'кукуруза', status: false},
        {quest: 'Под кого обычно убивается твой парень(музыкант)?', answer: 'роки', status: false},
        {quest: 'Из какой песни строчка "Висюльки на моих руках"?', answer: 'конракт', status: false},
        {quest: 'Слово из четырез букв?', answer: 'анал', status: false},
        {quest: 'Какой алкоголь мы пили с тобойв первый раз?', answer: 'сидр', status: false},
        {quest: 'Угадай загадку: "Волосатая головка за щекой щекочет ловко."', answer: 'зубная щетка', status: false},
        {quest: 'Столица мексики?', answer: 'мехико', status: false},
        {quest: 'Из трех букв состоит, \nКогда работает - стоит.\nНа «Х» начинается, \nКогда кончает - кланяется', answer: 'хор', status: false},
        {quest: 'Наше самое частое слово за последние полгода?', answer: 'тютють', status: false},
        {quest: 'Название нашей баскетбольной команды?', answer: 'ванильный гром', status: false},
        {quest: 'Номер дома в моем адресе на Ленинском?', answer: '78', status: false},
        {quest: 'Ни одно да...', answer: 'пизда', status: false},
        {quest: 'Как чаще всего я называю тебя уменьшительным от животного', answer: 'котя', status: false},
        {quest: 'Что ты мне ответишь в один из важнейших для нас моментов?', answer: 'да', status: false},
    ])

  return (
    <div className='quests'>
        {quests.map((quest, index) =>
          !quest.status &&
            <Quest quests={quests} setQues={setQuest} scores={scores} setScores={setScores} key={index} index={index+1} quest={quest}/>
        )}
    </div>
  )
}

export default Quests