import React, { useEffect, useState } from 'react'
import { Alert } from 'react-bootstrap'

const Quest = ({quests, setQues, setScores, scores, index, quest}) => {
    const [answer, setAnswer] = useState('')
    const [alert, setAlert] = useState('')

    const check = () => {
        if (answer.toLowerCase() === quest.answer.toLowerCase()){
            const id = quests.indexOf(quest)
            setScores(scores + 2)
            let copy = Object.assign([], quests);
            copy[parseInt(id)].status = true
            setQues(copy)
            setAnswer('')
        }
        else{
            setAlert(true)
            setTimeout(() => {
                setAlert(false)
            }, 2000);
            setAnswer('')
        }
        
    }

  return (
    <div className='quest'>
        {alert && 
        <Alert style={{fontSize: '30px'}}variant={'danger'}>
        Неправильно, попробуй еще раз дорогая!!!
      </Alert>
      }
        <div className="">{index}. {quest.quest}</div>
        <div className="myrow">
            <input value={answer} onChange={(e) => setAnswer(e.target.value)} type="text" />
            <button onClick={() => check()}>Проверить</button>
        </div>
    </div>
  )
}

export default Quest