import React, { useEffect, useState } from 'react'
import Map from './Map'

const Presents = ({setPresents, progress}) => {

  const [value, setValue] = useState('')
  const codes = ['7290102', '8000500']
  let number = 0

  if (progress===5){
    number = 0
  }else if(progress === 10){
    number = 1
  }else{
    number = 2
  }
  
  useEffect(() => {
    codeCheck();
  }, [value])
  const codeCheck = () => {
    if (value == codes[number]){
      setPresents(false)
    }
  }

  if (progress===5){
  return (
    <div className="rules">
      <div className="rules_content">
          <h1 style={{marginBottom: '30px'}}>Бонусный квест</h1>
          <div className="rules_text">
            <div>Что-то тут не так, чего-то не хватает этой принцессе... Ищи в коробке от 'снаВ'!!</div>
            <div>Чтобы идти дальше ты должна ввести первые 7 цифр кода...</div>
            <input value={value} onChange={(e) => {setValue(e.target.value)}} className='mt-5' type="text" />
          </div>
      </div> 
    </div>
  )}

  if (progress===10){
    return (
      <div className="rules">
        <div className="rules_content">
        <h1 style={{marginBottom: '30px'}}>Бонусный квест</h1>
          <div className="rules_text">
            <div>Нужно срочно подкрепиться!! Ищи под тем, где обычно происходит что-то очень интересное!</div>
            <div>Чтобы идти дальше ты должна ввести первые 7 цифр кода...</div>
            <input value={value} onChange={(e) => {setValue(e.target.value)}} className='mt-5' type="text" />
          </div>
        </div> 
      </div>
    )}

    if (progress===18){
      return (
        <div className="rules">
          <div className="rules_content">
              <h1 style={{marginBottom: '30px'}}>Поздравление</h1>
              <div className="rules_text">
                <div>Е-хо-хо!!! Ты собрала все кусочки карты сокровищ!<br/>
                      Я поздравляю тебя с днем рождения и желаю веселых поисков! <br/>
                      <div className='full_map'></div>
                </div>
        
              </div>
          </div> 
        </div>
      )}
}

export default Presents